@mixin indents-reset {
    margin: 0;
    padding: 0;
}

@mixin list-reset {
    @include indents-reset;
    list-style: none;
}

@mixin section {
    width: 100%;
    height: 100vh;
}

@mixin link {
    font-size: 13px;
    line-height: 30px;
    font-weight: 500;
    text-transform: uppercase;
    color: #ffffff;
    transition: 0.2s;
}

@mixin title {
    font-family: $gagalin;
    font-size: 72px;
    line-height: 64px;
    font-weight: 500;
    color: #f9b43b;
}